footer div[class^=col-].yellow {
      border-color: #feb960; }
footer div[class^=col-].yellow .footer-title {
        color: #feb960; }
footer .footer-col {
    padding: 0 15px; }
footer .footer-col p {
      color: #fff;
      line-height: 1.7; }
@media (max-width: 767.98px) {
        footer .footer-col p {
          text-align: center; } }
footer .footer-col p:last-of-type {
        margin-bottom: 0; }
footer .footer-col .footer-title {
      font-size: 18px;
      margin-bottom: 25px; }
footer .footer-col a:not(.footer-logo) {
      display: inline-block;
      color: #fff;
      font-size: 30px;
      line-height: 1em;
      vertical-align: middle; }
footer .footer-col a:not(.footer-logo) i {
        display: inline-block;
        padding-top: 3px; }
footer .footer-col a:not(.footer-logo) i:first-of-type:before {
          margin-left: 0; }
footer .footer-col a:not(.footer-logo) i:before {
          margin: 0 4px; }
footer .footer-col a:not(.footer-logo):hover {
        color: #007bff; }
footer .footer-logo {
    display: block;
    text-align: center;
    font-size: 50px;
    font-family: "WisdomScriptAJ";
    color: #fff; }
footer .footer-logo > span {
      color: #fc5f45; }
